body {
  font-size: 1.5rem;
  font-family: 'Krub', sans-serif;
  background-image: linear-gradient(to top, #dfe9f3 0%, #ffffff 100%); }

.contenedor {
  max-width: 125rem;
  margin: 0 auto; }

.boton {
  background-color: #030a1f;
  color: #ffffff;
  padding: 1rem 3rem;
  margin-top: 3rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: .5rem;
  width: 90%;
  text-align: center;
  border: none; }

.boton:hover {
  cursor: pointer; }

.sombra {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(56, 55, 56, 0.51);
  -moz-box-shadow: 0px 5px 15px 0px rgba(56, 55, 56, 0.51);
  box-shadow: 0px 5px 15px 0px rgba(56, 55, 56, 0.51);
  background-color: #ffffff;
  padding: 1rem;
  border-radius: 1rem; }

/* Tipografia */
h1 {
  font-size: 3rem; }

h2 {
  font-size: 2.8rem; }

h3 {
  font-size: 1.8rem; }

h1, h2, h3 {
  text-align: center; }

/* Titulos */
.titulo {
  color: #ffffff; }

.titulo span {
  font-size: 2rem; }

@media (min-width: 768px) {
  .boton {
    width: auto; } }

/* Utilidades */
.w-sm-100 {
  width: 100%; }

@media (min-width: 768px) {
  .w-sm-100 {
    width: auto; } }

.flex {
  display: flex; }

.alinear-derecha {
  justify-content: flex-end; }

/* Navegacion principal */
.nav-bg {
  background-color: #030a1f; }

.mobile-menu {
  color: #ffffff;
  font-size: 2rem;
  text-align: center;
  display: block;
  padding: 2rem 0; }

#nav:target {
  max-height: 80rem; }

.navegacion-principal {
  display: flex;
  flex-direction: column;
  max-height: 0;
  transition: .5s; }

@media (min-width: 768px) {
  .navegacion-principal {
    max-height: unset; }
  .mobile-menu,
  .cerrar {
    display: none !important; } }

@media (min-width: 768px) {
  .navegacion-principal {
    flex-direction: row;
    justify-content: space-between; } }

.navegacion-principal a {
  display: block;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 700;
  padding: .7rem; }

@media (min-width: 768px) {
  .navegacion-principal a {
    padding: 1rem; } }

.navegacion-principal a:hover, .mobile-menu:hover, .boton:hover {
  background-color: #ffc107;
  color: #212121; }

/* Hero */
.parallax::before {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  -webkit-filter: brightness(0.8);
  filter: brightness(0.8); }

.hero {
  /* background-image: url(../img/DayTrader.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; */
  height: 45rem;
  position: relative;
  margin-bottom: 2rem; }

.hero::before {
  background-image: url(../img/DayTrader.webp);
  content: ''; }

.contenido-hero {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  /*formato antiguo*/
  width: 100%;
  height: 100%;
  /*Para alinear horizontal y verticalmente*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.contenido-hero h2, .contenido-hero p {
  color: #ffffff; }

/* Servicios */
.servicios {
  margin-bottom: 5rem; }

@media (min-width: 768px) {
  .servicios {
    display: grid;
    /* grid: 30rem 30rem 30rem / 10rem 20rem 30rem; La forma corta*/
    grid-template-columns: repeat(2, 1fr);
    column-gap: 1rem; } }

.s2 {
  margin-bottom: 1rem; }

.servicio {
  display: flex;
  flex-direction: column;
  align-items: center; }

.servicio h3 {
  color: #030a1f;
  margin: 1.5rem 0 .5rem 0; }

.servicio p {
  line-height: 1;
  text-align: center; }

.servicio .iconos {
  height: 15rem;
  width: 15rem;
  background-color: #ffc107;
  border-radius: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center; }

/* Contacto */
.s3 {
  margin: 0 auto; }

@media (min-width: 768px) {
  .s3 {
    max-width: 80%; } }

.formulario {
  background-color: #757575;
  width: 60rem;
  max-width: 100%;
  margin: 0 auto;
  padding: 2rem;
  border-radius: 1rem; }

.formulario fieldset {
  border: none; }

.formulario legend {
  text-align: center;
  width: 100%;
  font-size: 1.8rem;
  text-transform: uppercase;
  margin-bottom: 2rem;
  font-weight: 700;
  color: #ffc107; }

@media (min-width: 768px) {
  .contenedor-campos {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto 20rem;
    column-gap: 1rem; }
  .campo:nth-child(3), .campo:nth-child(4) {
    grid-column: 1 / 3; } }

.campo {
  margin-bottom: 1rem; }

.campo label {
  color: #ffffff;
  font-weight: bold;
  margin-bottom: .5rem;
  display: block; }

.campo textarea {
  height: 20rem; }

.input-text {
  width: 100%;
  border: none;
  padding: 1.5rem;
  border-radius: .5rem; }

/* Footer */
.footer {
  text-align: center; }

/* Informacion del fondo */
@media (min-width: 768px) {
  .graficos {
    display: flex;
    justify-content: space-between; } }

.grafico-fondo {
  display: block;
  width: 61rem;
  max-width: 100%;
  height: 38rem;
  margin: 0 auto;
  border: none;
  overflow: scroll; }

.informacion-fondo {
  line-height: 2;
  color: #030a1f; }
  .informacion-fondo h3 {
    text-align: left; }

@media (min-width: 768px) {
  .informacion-fondo {
    margin: 0 10rem; } }

.fondo-tabla {
  display: flex;
  gap: 5rem; }
  .fondo-tabla .fondo-columna {
    flex: 1; }
    .fondo-tabla .fondo-columna p {
      font-weight: 700; }

@media (min-width: 768px) {
  .fondo-tabla {
    margin: 0 50rem 0 10rem; } }

.tabs {
  display: flex;
  justify-content: center;
  gap: .5rem; }
  .tabs button {
    width: 20rem;
    max-width: 100%; }
  .tabs .actual {
    background-color: #ffc107;
    color: #212121; }

.seccion {
  display: none; }

.mostrar-seccion {
  display: block !important; }

.preguntas {
  margin: 0 2rem; }

.contenedor-pregunta {
  background-color: #dfe9f3;
  padding: 2rem;
  margin-top: 2rem;
  border-radius: 1rem; }
  .contenedor-pregunta .pregunta {
    font-size: 2rem;
    font-weight: 700; }
  .contenedor-pregunta .respuesta, .contenedor-pregunta li {
    line-height: 2; }

.contenedor-datos-fondo {
  display: block;
  width: 40rem;
  max-width: 100%;
  margin: 0 auto;
  border: 1px solid #757575;
  border-radius: 2rem; }
  .contenedor-datos-fondo h3 {
    text-align: left;
    border-bottom: 1px solid #757575;
    margin: 0;
    padding: 1rem 0;
    background-color: #dfe9f3;
    border-top-left-radius: 2rem;
    border-top-right-radius: 2rem; }

.datos-fondo {
  border: none;
  display: flex;
  justify-content: space-between;
  padding: 0 2rem; }
  .datos-fondo .label-datos {
    font-weight: 700; }
